<template>
    <div>
        <router-view />
        <div class="header"></div>
        <div class="register-box">
            <!-- <div class="arrow-left" @click="closeRegister"> -->
            <div class="arrow-left" >
                
                <router-link to="/mine">
                   <van-icon name="arrow-left" size="30px"/>                
                </router-link>
            </div>

            <div class="register-content">
                <div class="logo-box">
                    <div class="logo">上传头像</div>
                </div>

                <div class="userInformation">
                    <form>

                    <van-cell-group>
                        <!-- 用户名 --> 
                        <van-field
                            type="text"
                            placeholder="请输入用户名"
                            label="用户名:"
                            left-icon="contact"
                            v-model="registerData.username.value"
                            :error-message="registerData.username.errorMessage"
                            @input="inputUserName(registerData.username.value)"
                        />
                        
                        <!-- 密码 -->
                        <van-field
                            :type="registerData.password.type"
                            label="密码"
                            placeholder="请输入密码"
                            left-icon="lock"
                            :right-icon="registerData.password.icon"
                            v-model="registerData.password.value"
                            @click-right-icon="checkpossword1"
                            :error-message="registerData.password.errorMessage"
                            @input="inputPassword(registerData.password.value)"

                        />

                        <!-- 确认密码 -->
                        <van-field
                            :type="registerData.truepassword.type"
                            label="确认密码"
                            placeholder="请输入确认密码"
                            :right-icon="registerData.truepassword.icon"
                            :error-message="registerData.truepassword.errorMessage"
                            left-icon="lock"
                            v-model="registerData.truepassword.value"
                            @click-right-icon="checkpossword2"
                            @input="inputTruePwd(registerData.password.value,registerData.truepassword.value,registerData.username.value)"                           
                        />                     
                    </van-cell-group>

                    
                    <div class="register-button" >
                        <van-button class="register-btn" :loading="false" type="info" block loading-text="注册中..."  text="注册" :disabled="isdisabled" @click="register()" />
                    </div>
                    </form>

                    <div class="login"><span>已有账号？</span><router-link to="login"><span class="login">登录</span></router-link></div>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {

        name:'register',

        data(){
            return{

                //判断是否开启注册按钮
                isdisabled:true,

                //注册信息
                registerData:{
                    username:{
                        value:'',
                        errorMessage:''
                    },
                    password:{
                        value:'',
                        type:'password',
                        icon:'closed-eye',
                        errorMessage:''
                    },
                    truepassword:{
                        value:'',
                        type:'password',
                        icon:'closed-eye',
                        errorMessage:''
                    },
                }
            }
        },

        methods:{


            checkpossword1(){

                let open1 = this.registerData.password.type ==='password'
                this.registerData.password.type = open1 ? 'text' : 'password'
                this.registerData.password.icon = open1 ? 'eye-o' : 'closed-eye'
            },

            checkpossword2(){
                let open2 = this.registerData.truepassword.type === 'password'
                // console.log('this1 ==>',this.registerData.truepassword.type)
                // console.log('open2 ==>',open2)           
                this.registerData.truepassword.type = open2 ? 'text' : 'password'
                // console.log('this ==>',this.registerData.truepassword.type)
                this.registerData.truepassword.icon = open2 ? 'eye-o' : 'closed-eye'
            },

            // 验证用户名
            inputUserName(value){
                let usernamereg=/^[A-Za-z]\w{3,12}$/
                if(!usernamereg.test(value)){
                    this.registerData.username.errorMessage = '用户名格式错误(请用字母开头的3-12位字母或数字)'
                }else{
                    this.registerData.username.errorMessage = ''
                }
            },

            // 验证密码
            inputPassword(value){
                let pwdReg = /^(?=.*[A-Z])(?=.*[a-z])\w{6,16}$/;
                if(!pwdReg.test(value)){
                    this.registerData.password.errorMessage = '密码必须含有大写、小写、数字'
                }else{
                    this.registerData.password.errorMessage = ''
                }

            },

            //验证确认密码
            inputTruePwd(value1,value2,value3){
                if(value1 == value2){
                   this.registerData.truepassword.errorMessage = '' 
                   
                }else{
                    this.registerData.truepassword.errorMessage = '两次密码不一致' 
                }
                if((value3 !='') && (value1 == value2)){
                    this.isdisabled = false;
                }
                
            },

            // 点击注册的方法
            register(){
                let _this = this;

                let randomTime = Math.random() * 5 * 1000;
                // console.log('randomTime ==>',randomTime)

                setTimeout(function (){

                    //获取用户的信息数据
                    let userData = localStorage.getItem('userData')

                    //防止空用户
                    userData = userData ? JSON.parse(userData) : [];
                    
                    for(let i=0; i<userData.length;i++){
                        if(userData[i].username == _this.registerData.username.value){
                            _this.$toast({
                                duration: 2000,
                                message: '用户已存在'
                            })
                            return;
                        }
                    }
                    _this.$toast({
                        duration: 2000,
                        message: '注册成功'
                    })

                    let time = new Date().getTime();

                    //记录用户的信息
                    let userInformation = {

                        //用户名
                        username: _this.registerData.username.value,

                        //密码
                        password: _this.registerData.password.value,

                        //时间
                        time
                    };
                    // 把用户信息放进数组userData中
                    userData.push(userInformation);

                    // console.log('userData ==>',userData)
                    // 信息保存到本地
                    localStorage.setItem('userData',JSON.stringify(userData));

                    setTimeout(function (){
                        // 跳转到登录页面
                        _this.$router.push({name:'login'});
                    },3000)

                },randomTime)
              
            }
        }
        
    }
</script>

<style lang="less" scoped>

    .header{
        height: 24px;
    }
    .register-box{
    width: 100%;
    // height:17.8rem;
    background: #fff;
    z-index: 20;
    position:absolute;
    top: 0px;
    left: 0;
    // display: none
    margin-top: 24px;

    .arrow-left{
        height: .8rem;
        margin-left: .2rem;
    }

    .logo-box{
        width: 100%;
        height: 3rem;
        background: #f5f5f5;
        position: relative;
        .logo{
            width: 2rem;
            height: 2rem;
            margin:0 auto;
            background: #ff3539;
            position:absolute;
            top: .5rem;
            left: 0;
            right: 0;
            border-radius: 50%;
            text-align: center;
            line-height: 2rem;
            font-size: .4rem;
            color: #fff;
        }
    }
    .userInformation{
        margin-top: .2rem;
        .register-button{
        margin-top: 1rem;
        }
    }  
    .login{
        margin-top: 1rem;
        font-size: .5rem;
        text-align: center;

        .login{
            color: #00B0F0;
            font-size: .6rem;
        }
    }
    // .van-hairline--top::after {
        
    //     // border-top-width: 0 !important;
    //     >[class*=van-hairline]::after{
    //         border:0 solid #fff;
    //     }
    // }
    // [class*=van-hairline]::after {
  
    
}

</style>